import Vue from 'vue';
import {Style,createAPI,Toast} from 'cube-ui';
Vue.use(Toast)
function toast(that,msgs){
	const toast = that.$createToast({
    	time: 2000,
    	type:"txt",
    	txt: "<div style='text-align:center;'>"+msgs+"</div>"
    })
    toast.show();
}
//
export const IDREG=(that,val='',msg='')=>{
	const reg=/\d{17}[0-9Xx]|\d{15}/g;
	let flag=reg.test(val);
	if(!flag){
		toast(that,""+msg)
	}
	return flag;
};
//
export const TELREG=(that,val='',msg='')=>{
	const reg=/^[1][3,4,5,6,7,8,9][0-9]{9}$/;
	let flag=reg.test(val);
	if(!flag){
		toast(that,"'13''14''15''16''17''18''19' 11")
	}
	return flag;
};
//input
export const ISINPUTNULL=(that,val='',msg='')=>{
	if(!val){
		toast(that,msg+"")
	}
	return !!val;
};
//
/**
* @param idCard
*/
export const COMPRESSBIRTH=(that,idCard='',birth)=>{
    var birthday = "";
    if(idCard != null && idCard != ""){
        if(idCard.length == 15){
            birthday = "19"+idCard.slice(6,12);
        } else if(idCard.length == 18){
            birthday = idCard.slice(6,14);
        }   
        birthday = birthday.replace(/(.{4})(.{2})/,"$1-$2-");
        //:1990-01-01
        if(birthday!=birth){
        	toast(that,"");
        	return false
        }else{
        	return true
        }
    }
    return false;
};
//
/**
* @param idCard
*/
export const COMPRESSSEX=(that,idCard='',sex)=>{
    var sexStr = '';
    if (parseInt(idCard.slice(-2, -1)) % 2 == 1) {
        sexStr = '0';
    }
    else {
        sexStr = '1';
    }
    if(sexStr!=sex){
    	toast(that,"");
    	return false;
    }else{
    	return true;
    }
};


